.NavBar {
  background-color: #262727;
  height: 50px;
  margin-top: 20px;
}

.FlexAlign {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  height: 50px;
}

.FlexAlign a {
  color: white;
  font-size: 20px;
  font-weight: 600;
  padding: 15px 10px;
  background-color: #191a1a;
}

.FlexAlign a:hover {
  color: #202424;
  background-color: #d7e4e4;
}